import React from "react"
import styled from "styled-components"
import { HIs, HJson } from "@macashipo/mlib"
const Wrap = styled.div`
  margin-bottom: 0.5rem;
  min-width: 300px;
  & input {
    padding: 0.25rem;
    line-height: 1rem;
    height: 1.5rem;
  }
  & .alert {
    padding: 0.5rem;
    margin-bottom: 0.5rem;
  }
`
export default function MTFCWrapControl(props) {
  const { oneFilterControl } = props
  // console.warn("render MTFCWrapControl:", props)
  let _title = oneFilterControl.title
  if (HIs.isJsonString(_title)) {
    let _obj = HJson.getObj(_title, {})
    if (_obj && _obj.title) {
      _title = _obj.title
    }
  }
  return (
    <Wrap className="mtfcw-ap col-4">
      <div>{_title}</div>
      <div>{props.children}</div>
    </Wrap>
  )
}
