import React from "react"
import { MyBase } from "@macashipo/mlib"
import WrapControl from "./_wrapControl"
import useConfigTableFilterControl from "../../hooks/useConfigTableFilterControl"
import styled from "styled-components"
const Wrap = styled.div`
  & .form-control {
    padding: 0px;
    height: auto;
    border: 0px;
  }
`
export default function MTFCCheckbox(props) {
  const ctfc = useConfigTableFilterControl({ props })
  const { currentValue } = ctfc
  // console.warn("render MTFCCheckbox:", currentValue)
  return (
    <WrapControl {...props}>
      <Wrap className="mtfc-checkbox">
        <MyBase
          type="checkbox_yesno"
          className="form-control"
          value={currentValue}
          onChange={v => {
            console.warn("onChange:", v)
            if (v != null) {
              ctfc.updateData(v)
            } else {
              ctfc.clearValue()
            }
          }}
        />
      </Wrap>
    </WrapControl>
  )
}
