import { HIs, HJson } from "@macashipo/mlib"
import { kfnGetTitleControl } from "src/helpers/fnList"

const fnListOfFilterControl = {
  init({ props, configFilterControl }) {
    // const configFormControl = props.configFormControl || {}
    // const { fieldName, more } = configFormControl
    let _fnList = {
      ...props.fnList,
    }
    // const more = oneTableCell.more
    // _fnList[kfnGetApiPath] = () => {
    //   return HUtils.runFuntion(oneTableCell.fnList, kfnGetApiPath, [])
    // }
    _fnList[kfnGetTitleControl] = () => {
      let _title = configFilterControl.title
      if (HIs.isJsonString(_title)) {
        let _obj = HJson.getObj(_title, {})
        if (_obj && _obj.title) {
          _title = _obj.title
        }
      }
      return _title
    }
    return _fnList
  },
}

const helpersOfFilterControl = {}
export { helpersOfFilterControl, fnListOfFilterControl }
