/*eslint-disable @typescript-eslint/no-unused-vars */
import { HJson, MyTableFilterControlHelpers } from "@macashipo/mlib"
import React from "react"
import { fnListMyPage } from "src/helpers/fnList"
import { fnListOfFilterControl } from "./fnListOfFilterControl"
import useForceUpdate from "./useForceUpdate"

// var countForceUpdate = 0
// const countForceUpdateMax = 200
function useConfigTableFilterControl({ props } = {}) {
  // countForceUpdate++
  const refConfigFilterControl = React.useRef(props.oneFilterControl)
  const configFilterControl = refConfigFilterControl.current
  const { type, currentValue, more } = configFilterControl //title, fieldName, optionKey, onePage
  // const [valueFU, setValueFU] = useState(0) // integer state

  const refFnList = React.useRef(
    fnListOfFilterControl.init({ props, configFilterControl })
  )
  const fnList = refFnList.current

  const forceUpdate = useForceUpdate()
  // const forceUpdate = () => {
  //   console.warn("forceUpdate", countForceUpdate)
  //   setValueFU(valueFU + 1)
  // }

  if (props.custom) {
    if (configFilterControl["_applyCustom"] == null) {
      if (props.custom.more) {
        configFilterControl.more = {
          ...configFilterControl.more,
          ...props.custom.more,
        }
      }
      configFilterControl["_applyCustom"] = true
    }
  }

  if (configFilterControl) {
    if (configFilterControl._fnGetFilterOutFromValue == null) {
      if (
        MyTableFilterControlHelpers.ConfigByType &&
        MyTableFilterControlHelpers.ConfigByType[type]
      ) {
        const fn = value => {
          return MyTableFilterControlHelpers.ConfigByType[type]["getFilterOut"](
            value,
            configFilterControl
          )
        }
        configFilterControl.setFnGetFilterOutFromValue(fn)
      } else if (
        MyTableFilterControlHelpers.ConfigByType &&
        MyTableFilterControlHelpers.ConfigByType["_default"]
      ) {
        const fn = value => {
          return MyTableFilterControlHelpers.ConfigByType["_default"][
            "getFilterOut"
          ](value, configFilterControl)
        }
        configFilterControl.setFnGetFilterOutFromValue(fn)
      }
    }
    if (configFilterControl._loadFirst !== false) {
      if (configFilterControl.setFromComponent) {
        configFilterControl.setFromComponent({
          forceUpdate: () => {
            console.warn("forceupdateee")
            forceUpdate()
          },
        })
      }
      configFilterControl.loadLocal()
      configFilterControl._loadFirst = false
      // console.warn("load first", configFilterControl)
    }
  }

  return {
    forceUpdate,
    configFilterControl,
    currentValue,
    more,
    fnList,
    clearValue: () => {
      if (configFilterControl && configFilterControl.updateCurrentValue) {
        console.warn("clear Value", props, configFilterControl)
        let _onePage = fnListMyPage.fnGetOnePage(fnList)
        configFilterControl.clear()
        configFilterControl.updateCurrentValue(null)
      }
      forceUpdate()
    },
    updateData: v => {
      if (configFilterControl && configFilterControl.updateCurrentValue) {
        configFilterControl.updateCurrentValue(v)
      }
      forceUpdate()
    },
    getSourceData: () => {
      //bo sung source list tu json string Source
      // console.warn("configFilterControl", configFilterControl)
      if (
        configFilterControl &&
        configFilterControl.optionKey &&
        configFilterControl.optionKey.startsWith("[")
      ) {
        return HJson.getObj(configFilterControl.optionKey, [])
      }
      if (configFilterControl && configFilterControl.getSourceData) {
        return configFilterControl.getSourceData()
      }
      // if (onePage && onePage._dataOptions && onePage._dataOptions[optionKey]) {
      //   return onePage._dataOptions[optionKey]
      // }
      return []
    },
  }
}

export default useConfigTableFilterControl
